.footer {
    /* padding-top: 100px; */
    color: #FAFAFA;
    background-image: url('../../../images/bgc/404_spinach_bottom_bg_mob_1x.png');
    background-repeat: no-repeat;
    background-position: 190px 330px;
   
}

@media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .footer {
    background-image: url('../../../images/bgc/404_spinach_bottom_bg_mob_2x.png')
  }
}

@media screen and (min-width: 768px) {
    .footer {
        /* padding-top: 200px; */
        background-image: url('../../../images/bgc/404_spinach_bottom_bg_tab_1x.png');
        /* background-position: 0px 0px; */
        background-position: 450px 240px;
    }
    @media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .footer {
    background-image: url('../../../images/bgc/404_spinach_bottom_bg_tab_2x.png')
  }
}
}

@media screen and (min-width: 1440px) {
    .footer {
        padding-top: 118px;
        background-image: url('../../../images/bgc/404_spinach_bottom_bg_desk_1x.png');
    }
    @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    .footer {
      background-image: url('../../../images/bgc/404_spinach_bottom_bg_desk_2x.png')
    }
  }
}

.footer_text {
    display: flex;
    justify-content: center;
    padding: 28px 0;
    font-size: 10px;
    font-weight: 500;
    line-height: 10px;
    letter-spacing: -0.01em;
    text-align: left;
    color: #22252A;
    opacity: 0.5;
  
}

@media screen and (min-width: 768px) {
    .footer_text {
    font-size: 14px;
    line-height: 14px;
    padding: 32px 0;
     }
}

